import React, { Component } from 'react';
import {
  Responsive,
  Segment,
  Visibility,
} from 'semantic-ui-react';
import { MastHead } from '../views/rows';
import AppMenu from './Menu';

export default class DesktopContainer extends Component {
  state = {};

  handleMenuTransition = (fixed) => this.setState({ fixed });

  render() {
    const { children } = this.props;
    const { fixed } = this.state;

    return (
      <React.Fragment>
        <Responsive minWidth={Responsive.onlyComputer.minWidth}>
          <Visibility
            once={false}
            onBottomPassed={() => this.handleMenuTransition(true)}
            onBottomPassedReverse={() => this.handleMenuTransition(false)}
          >
            <Segment
              className="masthead desktop-masthead"
              inverted
              textAlign="center"
              vertical
              style={{ padding: '0px' }}
            >
              <div className="content desktop-masthead-content">
                <AppMenu fixed={fixed} />
                <MastHead />
              </div>
            </Segment>
          </Visibility>

          {children}
        </Responsive>
        <Responsive
          minWidth={Responsive.onlyTablet.minWidth}
          maxWidth={Responsive.onlyTablet.maxWidth}
        >
          <Visibility
            once={false}
            onBottomPassed={() => this.handleMenuTransition(true)}
            onBottomPassedReverse={() => this.handleMenuTransition(false)}
          >
            <Segment
              className="masthead desktop-masthead"
              inverted
              textAlign="center"
              vertical
              style={{ padding: '0px' }}
            >
              <div className="content desktop-masthead-content">
                <AppMenu fixed={fixed} />
                <MastHead tablet />
              </div>
            </Segment>
          </Visibility>
          {children}
        </Responsive>
      </React.Fragment>
    );
  }
}
