import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { ROUTES } from './enums/constants';
import NoMatch from './views/NoMatch';

const App = () => (
  <Router>
    <Switch>
      {
        ROUTES
          .filter(({ splash }) => (process.env.REACT_APP_SPLASH_PAGE_ONLY ? splash : !splash))
          .map(({ path, component }) => <Route key={path} exact={path === '/'} path={path} component={component} />)
      }
      <Route path="*" component={NoMatch} />
    </Switch>
  </Router>
);

export default App;
