import React from 'react';
import { Grid, Header, Segment } from 'semantic-ui-react';
import Image from './Image';
import '../../../styles/CoreTeam.css';
import { PEOPLE } from '../../../enums/constants';

const renderPeople = (list, tabletCol, computerCol) => (
  list.map(({ url, name = '' }) => (
    <Grid.Column centered="true" key={name} mobile="16" tablet={tabletCol} computer={computerCol} style={{ height: 'fit-content' }}>
      <Grid.Row centered columns={16}>
        <Grid.Column centered="true">
          <Image name={name} />
        </Grid.Column>
      </Grid.Row>
      <Grid.Row>
        <p className="PersonName">{name}</p>
      </Grid.Row>
      <Grid.Row>
        <a href={url}>
          <p className="LinkedIn">in</p>
        </a>
      </Grid.Row>
    </Grid.Column>
  ))
);

export default () => (
  <Segment id="Team" vertical style={{ backgroundColor: '#D8D8D8', paddingTop: '3.75rem', marginTop: '2.75rem' }}>
    <div className="CoreGrid">
      <Grid style={{ width: '80%', margin: '0 auto' }} centered>
        <Grid.Row>
          <div className="CoreText">
            <Header as="h3">
              <div className="Title">
              Core Team
              </div>
            </Header>
            <p className="TeamBlurb">
              The MassClarity core team combines a deep background in big data,
              blockchain, artificial intelligence and data exchanges,
              with real-world business experience as entrepreneurs, designers and technologists.
            </p>
          </div>
        </Grid.Row>
        <Grid.Row centered only="mobile tablet" style={{ width: '85%' }}>
          { renderPeople(PEOPLE.slice(0, 4), 4, 4) }
          { renderPeople(PEOPLE.slice(4), 4, 4) }
        </Grid.Row>
        <Grid.Row centered only="computer" style={{ maxWidth: 750 }}>
          { renderPeople(PEOPLE.slice(0, 4), 4, 4) }
          { renderPeople(PEOPLE.slice(4), 4, 4) }
        </Grid.Row>
      </Grid>
    </div>
  </Segment>
);
