import React from 'react';
import {
  Header,
  Image,
  Grid,
} from 'semantic-ui-react';
import graph from '../../../images/spend-on-data.svg';

export default ({ style }) => (
  <div id="card" style={style}>
    <Grid>
      <Grid.Row>
        <Grid.Column width={16} floated="right" only="mobile tablet" textAlign="center">
          <Image style={{ margin: '0 auto', padding: '3rem 0 1rem' }} rounded size="large" src={graph} />
        </Grid.Column>
      </Grid.Row>
    </Grid>
    <div className="card-wrapper">
      <Header as="h3" style={{ fontSize: '2em' }}>
        Today's Challenges
      </Header>
      <p>
        <b>Data Delay</b> - attempting to make proactive business decisions on data that changes every day and is three or four weeks old is essentially impossible
      </p>
      <p>
        <b>Rigid Data</b> - being limited to the constraints of a data broker’s existing business model or panel participants means that true customization of a request is not currently feasible
      </p>
      <p>
        <b>Monopolistic Data</b> - being limited to one option, where it’s generally understood that the data is being modified to suit the agenda of the data provider is unacceptable
      </p>
      <p>
        <b>ML Sample Size</b> - size of test data for machine learning is often limited and landlocked
      </p>
      <p>
        <b>Traceability</b> - having no way to understand the life cycle of the customer or how the data was sourced means little insight can actually be had from the existing data
      </p>
      <br />
    </div>
  </div>
);
