import React from 'react';
import { Grid, Header } from 'semantic-ui-react';

export default ({ style, children, list }) => (
  <Grid.Column
    width={16}
    only="tablet mobile"
    textAlign="left"
    style={style}
  >
    <div>
      <Header as="h4" style={{ fontSize: '2em', color: 'white' }}>
        {children}
      </Header>
      {
        list ? list.map((item, i) => <p key={`${item}${i}`}>{item}</p>) : null
      }
    </div>
  </Grid.Column>
);
