import React, { Component } from 'react';
import { Form, Input, Button, Label } from 'semantic-ui-react';
import { awsLambda } from '../enums/rest';
import { isEmail, onEnvironment } from '../enums/helpers';

const EmailInput = ({ error, getRef, ...props }) => (
  <Input
    ref={getRef}
    type="email"
    placeholder="Email"
    icon={error ? 'warning' : null}
    iconPosition={error ? 'left' : null}
    error={error}
    {...props}
  />
);

export default class EmailEntryForm extends Component {
  state = {
    error: false,
    loading: false,
  };


  componentDidMount() {
    if (this.emailInput) {
      this.emailInput.focus();
    }
  }

  // eslint-disable-next-line consistent-return
  handleEmailSubmit = async () => {
    try {
      const { email } = this.state;

      const valid = isEmail(email);
      if (valid) {
        this.setState({ loading: true });
        const response = await awsLambda.post('emails', { email });
        if (response.status === 201) {
          this.setState({
            error: false,
            loading: false,
            message: "Thanks for registering! We'll be in touch!",
            email: null,
          });
          return setTimeout(() => this.setState({ message: null }), 1500);
        }

        const message = onEnvironment({ production: 'Email was registered!' });
        return this.setState({
          error: true,
          loading: false,
          message: `${message || `Unexpected status of ${response.status} returned`}`,
        });
      }
      this.emailInput.focus();
      this.setState({ message: 'Please provide a valid email' });
    } catch (err) {
      const { response } = err || {};
      const message = onEnvironment({ production: 'We were unable to save your email, we will look into it!' });
      this.emailInput.focus();

      if (response && response.status) {
        switch (response.status) {
          case 400:
            return this.setState({ error: true, loading: false, message: 'Invalid email provided' });
          case 500:
            return this.setState({
              error: true,
              loading: false,
              message: 'We were unable to save your email, we will look into it!',
            });
          default:
            return this.setState({
              error: true,
              loading: false,
              message: message || `RESPONSE ERROR: ${err.toString()}`,
            });
        }
      }
      if ((err.toString() || '').includes('Network Error')) {
        // eslint-disable-next-line max-len
        // todo: assuming the CORS issue is not fixed, we ignore network errors and treat them as a success
        return this.setState({ error: false, loading: false, message: "Thanks for registering! We'll be in touch!" });
      }
      return this.setState({ error: true, loading: false, message: `REQUEST ERROR: ${message || err.toString()}` });
    }
  };

  handleEmailChange = (e, { value: email }) => {
    this.setState({
      email,
      message: null,
      error: false,
      loading: false,
    });
  };

  render() {
    const { error, loading, message, email } = this.state;
    const { className, buttonContent, mobile } = this.props;
    const messageLabel = <Label basic color="violet" pointing={mobile ? 'above' : 'left'} content={message} />;
    const submitButton = (
      <Button
        color="pink"
        type="submit"
        loading={loading}
        fluid={mobile}
        content={buttonContent || 'Register for Updates'}
      />
    );
    return (
      <React.Fragment>
        {
          mobile ? (
            <Form onSubmit={this.handleEmailSubmit} style={{ width: '80%' }}>
              <Form.Field>
                <EmailInput
                  value={email || ''}
                  fluid
                  error={error}
                  className="mobile-email-input"
                  getRef={node => this.emailInput = node}
                  onChange={this.handleEmailChange}
                />
              </Form.Field>
              {message ? messageLabel : submitButton}
            </Form>
          )
            : (
              <Form
                className={className || 'email email-form'}
                onSubmit={this.handleEmailSubmit}
              >
                <EmailInput
                  value={email || ''}
                  error={error}
                  transparent
                  className="email-input"
                  getRef={node => this.emailInput = node}
                  onChange={this.handleEmailChange}
                />
                {message ? messageLabel : submitButton}
              </Form>
            )
        }
      </React.Fragment>
    );
  }
}
