import React from 'react';
import { Grid, Segment, Card } from 'semantic-ui-react';
import { ROAD_MAP } from '../../enums/constants';

export default () => (
  <Segment basic padded="very" id="Roadmap" style={{ marginTop: '2.75rem', paddingTop: '3.75rem' }}>
    <Grid>
      <Grid.Row centered columns={1}>
        <Grid.Column width={14}>
          <Segment basic>
            <div className="roadmap-title">Roadmap</div>
            <div className="software-is-sometimes">
          *Software is sometimes hard to predict, so there might be delays.
          We will update dates once closer to a milestone’s completion.
            </div>
          </Segment>
          <Segment basic>
            <Card.Group stackable doubling centered>
              {
            ROAD_MAP.map(({ quarter, year, tasks }, idx) => (
              <Card key={quarter + year} className="roadmap-card" style={{ marginRight: idx % 2 && 10 }}>
                <Card.Content className="roadmap-content">
                  <h2 className="roadmap-header ">{quarter}</h2>{' '}
                  <p className="inline">{year}</p>
                </Card.Content>
                <Card.Content extra>
                  <div className="roadmap-extra-content">
                    {tasks.map(item => <p key={item}>{item}</p>)}
                  </div>
                </Card.Content>
              </Card>
            ))
          }
            </Card.Group>
          </Segment>
        </Grid.Column>
      </Grid.Row>
    </Grid>
  </Segment>
);
