import React, { PureComponent } from 'react';
import { Container, Menu, Image, Grid } from 'semantic-ui-react';
import { HashLink as Link } from 'react-router-hash-link';
import { MENU_ITEMS } from '../enums/constants';
import EmailEntryForm from './EmailEntryForm';

const coin = require('../images/knockout-coin.svg');
const clarity = require('../images/mc-logo.svg');

const renderMenuItem = ({ active, style, ...props }) => ({ name }) => (
  <Menu.Item
    key={name}
    as={Link}
    to={`/#${name}`}
    name={name}
    active={active === name}
    link
    content={name}
    position="right"
    fitted="horizontally"
    style={{
      fontWeight: 'bold !important',
      fontSize: 16,
      opacity: 0.9,
      height: '100%',
      textAlign: 'right',
      marginBottom: 0,
      paddingTop: 26,
      backgroundColor: active === name && '#1B242B',
      borderColor: active === name && '#3F51B5',
      border: 'none',
      ...style,
    }}
    {...props}
  />
);

const renderMobileMenuItem = ({ active, style, ...props }) => ({ name }) => (
  <Grid.Column key={name} textAlign="center" style={{ fontWeight: 'bold' }}>
    <Menu.Item
      as={Link}
      to={`/#${name}`}
      name={name}
      active={active === name}
      link
      content={name}
      position="right"
      fitted="horizontally"
      style={{
        color: 'white',
      }}
      {...props}
    />
  </Grid.Column>
);

const MenuComponent = ({ mobile, fixed, children }) => (
  mobile
    ? (
      <Grid columns="3" centered style={{ marginLeft: '5vh', marginRight: '5vh' }}>
        {MENU_ITEMS.map(renderMobileMenuItem({ }))}
      </Grid>
    )
    : (
      <Menu
        className={fixed ? 'dark-background menu-short desktop-menu fade-in-animation' : 'menu desktop-menu'}
        fixed={fixed ? 'top' : null}
        inverted
        pointing
        secondary
        size="large"
        style={{ border: 'none' }}
      >
        {children}
      </Menu>
    ));

/* eslint-disable react/destructuring-assignment */
const TopMenu = (props) => (
  <Container fluid>
    <Menu.Item position="left" name="home" onClick={props.onClick} style={{ margin: 0 }}>
      <Image src={clarity} style={{ maxHeight: '2em', marginBottom: 15 }} />
    </Menu.Item>

    <Menu.Item style={{ position: 'absolute', left: '48%' }}>
      <Image src={coin} style={{ height: '4em' }} />
    </Menu.Item>

    <Menu.Item position="right">
      {MENU_ITEMS.map(renderMenuItem({ ...props, position: 'right' }))}
    </Menu.Item>
  </Container>
);

const FixedMenu = (props) => (
  <React.Fragment>
    <Menu.Item position="left" onClick={props.onClick} style={{ border: 'none' }}>
      <Image src={clarity} style={{ height: '90%', width: '65%', marginBottom: 8 }} />
    </Menu.Item>

    <Menu.Item position="right" className="menu-right-items">
      {MENU_ITEMS.map(renderMenuItem({ ...props, position: 'right' }))}
      <EmailEntryForm className="email menu-email-form" buttonContent="Register" />
    </Menu.Item>

  </React.Fragment>
);
/* eslint-disable react/destructuring-assignment */

export default class AppMenu extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { active: props.fixed ? 'Solution' : 'home' };
  }

  componentWillReceiveProps({ fixed }) {
    const { active } = this.state;
    if (fixed && active === 'home') {
      this.setState({ active: 'Solution' });
    } else if (!fixed && active !== 'home') {
      this.setState({ active: 'home' });
    }
  }


  handleMenuItemClick = (e, { name: active }) => {
    this.setState({ active });
  };

  render() {
    const { mobile, fixed } = this.props;
    const { active } = this.state;
    return (
      mobile
        ? <MenuComponent mobile {...this.props} onClick={this.handleMenuItemClick} />
        : (
          <MenuComponent {...this.props}>
            {
            fixed
              ? <FixedMenu active={active} onClick={this.handleMenuItemClick} />
              : <TopMenu active={active} onClick={this.handleMenuItemClick} />
          }
          </MenuComponent>
        )
    );
  }
}
