import React from 'react';
import { Image, Segment, Header, Grid } from 'semantic-ui-react';
import neilkataria from '../../../images/people/neilkataria.jpeg';
import andyellwood from '../../../images/people/andyellwood.jpeg';
import hunterpowers from '../../../images/people/hunterpowers.jpeg';
import kylekirsch from '../../../images/people/kylekirsch.jpeg';

const PEOPLE_MAP = {
  neilkataria,
  andyellwood,
  hunterpowers,
  kylekirsch,
};

// eslint-disable-next-line import/no-dynamic-require, global-require, max-len
// const getImage = name => require(`../../../images/people/${name.split(' ').join('').toLowerCase()}.jpeg`);
const getImage = name => PEOPLE_MAP[name.split(' ').join('').toLowerCase()] || null;
const getInitials = name => name.split(' ').reduce((output, word) => (word[0] ? output + word[0] : output), '');

export default ({ name }) => {
  const image = getImage(name);
  const MySegment = size => (
    <Segment style={{ height: size, width: size, display: 'block', margin: '0 auto', color: 'white', backgroundColor: 'black' }} circular textAlign="center">
      <Header as="h2" style={{ color: 'white', marginTop: 15 }}>
        {getInitials(name)}
      </Header>
    </Segment>
  );
  return image !== null ? <Image centered src={image} className="ui centered small circular image" /> : (
    <Grid>
      <Grid.Row centered>
        <Grid.Column width={16} only="mobile" textAlign="center">
          { MySegment(150) }
        </Grid.Column>
        <Grid.Column width={16} only="tablet" textAlign="center">
          { MySegment(105) }
        </Grid.Column>
        <Grid.Column width={16} only="computer" textAlign="center">
          { MySegment(150) }
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
};
