const Content = ({ children, mobile }) => (
  <Responsive minWidth={500}>
    <Header
      as="h5"
      content="A decentralized data commerce protocol"
      inverted
      style={{
        fontSize: mobile ? '1.5em' : '1.7em',
        fontWeight: 'normal',
        lineHeight: '41px',
        marginTop: mobile ? '1.5em' : '5em',
      }}
    />
    <Header
      as="h3"
      content="The future of commerce on the blockchain."
      inverted
      style={{
        fontSize: mobile ? '2em' : '46px',
        fontWeight: 'bold',
        marginBottom: '2.5vmin',
        marginTop: mobile ? '.5em' : '.2em',
      }}
    />
    {children}
  </Responsive>
);

const getWidth = (mobile, tablet) => {
  if (mobile) {
    return 16;
  }
  if (tablet) {
    return 8;
  }
  return 6;
};

export default ({ mobile, tablet }) => {
  const width = getWidth(mobile, tablet);
  const style = mobile ? { margin: '0 2vh' } : {};
  return (
    <Grid>
      <Grid.Row columns={mobile ? 1 : 2}>
        <Grid.Column textAlign="left" width={width} style={style} className="masthead-card-padding grow-up-animation">
          {
            mobile ? (
              <Grid className="mobile-masthead" centered>
                <Grid.Row>
                  <EmailEntryForm mobile={mobile} />
                </Grid.Row>
              </Grid>
            )
              : (
                <Content mobile={mobile}>
                  <EmailEntryForm mobile={mobile} />
                </Content>
              )
          }
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
};
